<template>
    <div class="quick-pay">
        <div class="card text-center">
            <div class="card-header">
                <img src="@/assets/flywire.png" height="20px"/>
            </div>
            <div class="card-body">
                <img v-if="client.logo" :src="client.logo" :alt="client.name" />
                <h4>{{ ui.title }}
                  <small class="text-muted">{{ ui.subTitle }}</small>
                </h4>
                <div v-if="formattedAmount" class="payment-info">
                    <h3>
                      <small>Please pay</small>
                      {{ formattedAmount }}
                    </h3>
                </div>
                <div v-if="ui.configErrors.length > 0 || ui.paymentErrors.length > 0" class="errors">
                    <template v-for="e in ui.configErrors">
                        <span :key="e">{{ e }}</span>
                    </template>
                    <template v-for="e in ui.paymentErrors">
                        <span :key="e.id">{{ e.msg }}</span>
                    </template>
                </div>
                <ul class="list-group list-group-flush">
                    <li v-if="displayParameters.length > 0" class="list-group-item">
                        <dl>
                            <template v-for="p in displayParameters">
                                <dt :key="p.key + '_dt'">{{ p.label }}</dt>
                                <dd :key="p.key + '_dl'">{{ p.value }}</dd>
                            </template>
                        </dl>
                    </li>
                </ul>
                <div v-if="canPay === true" class="form-group">
                    <label for="exampleFormControlSelect1">Where do you live?</label>
                    <select class="form-control" v-model="country">
                        <template v-for="c in portal.countries">
                            <option :key="c.code" :value="c.code">{{ c.name }}</option>
                        </template>
                    </select>
                </div>
            </div>
            <div class="card-footer">
                <button :disabled="!canPay" class="btn btn-primary" v-on:click="onPay">Make Payment</button>
            </div>
        </div>
    </div>
</template>

<script>

import { mapGetters, mapState } from 'vuex'

export default {
    name: 'quick-pay',
    computed: {
        ...mapState({
          ui: 'ui',
          portal: 'portal',
          payment: 'payment'
        }),
        ...mapGetters([
          'formattedAmount',
          'client',
          'displayParameters',
          'canPay'
       ]),
       country: {
        get () {
            return this.$store.state.payment.countryCode;
        },
        set (value) {
            this.$store.dispatch('paymentSetCountry', value)
        }
      }
    },
    mounted: function() {
      this.$store.dispatch('load');
    },
    methods: {
      onPay: function() {
        var that = this;
        var popup = null;

        this.$store.dispatch('pay')

        const config = {
          env: this.portal.env,
          recipientCode: this.portal.portalCode,
          amount: this.payment.amount,
          country: this.payment.countryCode,
          recipientFields: this.payment.parameters,
          requestPayerInfo: true,

          onInvalidInput: (errs) => {
            that.$store.dispatch('paymentSetErrors', errs)
          },
          onCompleteCallback: (args) => {
            that.$store.dispatch('complete', args);
              popup.close();
              this.$router.push('complete');
          }
        }

        popup = window.FlywirePayment.initiate(config);
        popup.render();
      }
    }
}
</script>

<style lang="scss">
    .quick-pay {
        width: 400px;
        margin: 50px auto;
    }
    h4, h3 {
        small {
            display: block;
            font-size: 1rem;
        }
    }
    .payment-info, .form-group { 
        margin-top: 8px;
    }
    

    .payment-info{ 
        background-color: #1274C4;
        color: white;
        margin: 0 -20px;
        padding: 5px 0;
    }

    .errors {
        background-color: #D3556B;
        color: white;
        margin: 5px -20px;
        padding: 5px 0;

        span {
            display: block;
        }
    }
</style>

