
export default {
  getRecipient(portalCode, env) {
    return fetch(`https://payment.${env == 'demo' ? 'demo.' : ''}flywire.com/v3/recipients/${portalCode}`)
      .then(response => {
        return response.json();
      })
  },
  getCountries(portalCode, env) {
    // TODO: Use either prod or demo URLs
    return fetch(`https://payment.${env == 'demo' ? 'demo.' : ''}flywire.com/v3/recipients/${portalCode}/countries`)
      .then(response => {
        return response.json();
      })
  }
}